<template>
  <div class="body">
    <div class="nav clear">
      <div class="logo"><router-link to="/home">Weeken</router-link></div>
			<ul>
				<li v-for="item in tabs" :key="item.path">
          <router-link :to="item.path" :class="$route.path.includes(item.path) ? 'router-link-active' : ''">{{item.name}}</router-link>
        </li>
			</ul>
		</div>
		<transition name="fade" mode="out-in">
      <router-view></router-view>
		</transition>
    <my-footer></my-footer>
  </div>
</template>

<script>
// const MyFooter = resolve => require(['./footer'], resolve)
import MyFooter from './footer'
export default {
  name: 'Navigator',
  components: { MyFooter },
  data () {
    return {
      tabs: [
        { path: '/memo', name: '便笺' },
        { path: '/note', name: '笔记' },
        { path: '/about', name: '关于' }
      ]
    }
  }
}
</script>

<style lang="less" scoped>
@font-face {
  font-family: "Lobster";
  src: url("../assets/font/Lobster-Regular.ttf");
  font-display: swap;
}

.body{
  width: 100%;
  height: 100%;
}

.nav{
  background: #2196f3;
  box-shadow: 0 0 14px rgba(0,0,0,.4);
  height: 70px;
  width: 100%;
  padding: 0 40px;
  // margin-bottom: 50px;
  // position: fixed;
  // top: 0;
  // left: 0;
  // right: 0;
  // z-index: 1000;

  .logo{
    float: left;
    transition: all .2s linear;
    &:hover{
      transform: scale(1.2);
    }
    & > a{
      width: 90px;
      font: 28px/70px Lobster;
      color: #fff;
    }
  }

  ul{
    float: right;

    li{
      display: inline-block;
      float: left;
      line-height: 70px;
      margin: 0 20px;
      white-space: nowrap;

      & > a{
        display: block;
        // width: 70px;
        height: 100%;
        font: 700 20px/70px "microsoft yahei";
        letter-spacing: 5px;
        color: rgba(255,255,255,.6);
        text-align: center;
        transition: all .2s linear;
        &:hover{
          color: rgba(255,255,255,.8);
        }
        &.router-link-active{
          color: #fff;
          transform: scale(1.2);
        }
      }

    }
  }
}
</style>



